import React from 'react';
import Grid from '@material-ui/core/Grid';
import Banner from '../components/Banner';
import ImgMediaCard from '../components/RSEimageCard';
import SimpleCard from '../components/RSEtextCard';
import ProyectRSE from '../components/RSEproyect'

class Home extends React.Component{
    render(){
        return (
        <div>
            <Banner />
            <Grid container>
                <Grid item lg={4}>
              <ImgMediaCard />
              </Grid>
              <Grid item lg={8}>
              <SimpleCard />
              </Grid>
            <ProyectRSE />
            </Grid>
        </div>    
        )
    }
};

export default Home;