import React, { Suspense } from 'react';
import { ThemeProvider } from '@material-ui/core/styles';
import { CssBaseline } from '@material-ui/core';
import LightTheme from './themes/LightTheme';
import { BrowserRouter,Switch, Route } from 'react-router-dom';
import Layout from './Layout';
import Home from '../pages/Home';
import Turismo from '../pages/Turismo';
import Sincro from '../pages/Sincro';
import NMG from '../pages/NMG';
import Recetas from '../pages/Recetas';
import Platzi from '../pages/Platzi';
import NotFound from '../pages/NotFound';


function App(){
    return (
        <Suspense fallback="loading">
          <ThemeProvider theme={LightTheme}>
            <CssBaseline />
                <BrowserRouter>
                    <Layout>
                        <Switch className="container">
                            <Route exact path="/" component={Home} />
                            <Route exact path="/turismo" component={Turismo} />
                            <Route exact path="/sincro" component={Sincro} />
                            <Route exact path="/nmg" component={NMG} />
                            <Route exact path="/recetas" component={Recetas} />
                            <Route exact path="/platzi" component={Platzi} />
                            <Route component={NotFound} />
                        </Switch>
                    </Layout>
                </BrowserRouter>
          </ThemeProvider>      
       </Suspense> 
    )
}

export default App